import styled from 'styled-components';
import { PageTitle, H2, H3, H5, H6, P } from '../../components/common/Typography';

export const Container = styled.div`
  max-width: 1040px;
  padding: 24px 0;
  margin: 0 auto;

  @media screen and (max-width: 425px) {
    padding: 0 !important;
    margin-right: -5px;
    margin-left: -5px;
  }
`;

export const SubText = styled(H6)`
  padding-top: 32px;
  @media screen and (max-width: 500px) {
    padding-top: 0;
    font-size: 14px;
    margin-top: -11px;
  }
`;

export const Row = styled.div`
  max-width: 992px;
  margin: auto;
  border-top: 3px solid #716161;
  transform: matrix(1, 0, 0, 1, 0, 0);
  padding: 28px 0;
  &:last-of-type {
    margin-bottom: 30px;
  }

  @media (min-width: 768px) {
    .right-text-panel {
      width: 65%;
    }
    .left-text-panel {
      flex: 1;
    }
  }
  &:last-of-type {
    border-bottom: 3px solid #716161;
  }

  @media screen and (max-width: 768px) {
    padding: 18px 0;

    .row-name {
      font-size: 18px;
    }
  }
  @media screen and (max-width: 425px) {
    &:first-of-type {
      margin-top: 50px;
    }
  }
`;
